.taroify-sidebar {
  width: var(--sidebar-width, var(--sidebar-width, 160px));
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
.taroify-sidebar:not(:last-child)::after {
  border-bottom-width: 1px;
}
.taroify-sidebar--disabled {
  color: var(--sidebar-disabled-color, var(--gray-5, #c8c9cc));
  cursor: not-allowed;
}
.taroify-sidebar--disabled:active {
  background: var(--sidebar-disabled-color, var(--gray-5, #c8c9cc));
}

.taroify-sidebar-tab {
  position: relative;
  box-sizing: border-box;
  display: block;
  padding: var(--sidebar-tab-padding, 40px var(--padding-sm, 24px));
  overflow: hidden;
  font-size: var(--sidebar-tab-font-size, var(--font-size-md, 28px));
  line-height: var(--sidebar-tab-line-height, var(--line-height-md, 40px));
  color: var(--sidebar-tab-color, var(--text-color, var(--gray-8, #323233)));
  cursor: pointer;
  user-select: none;
  background: var(--sidebar-tab-background-color, var(--background-color, var(--gray-1, #f7f8fa)));
}
.taroify-sidebar-tab:active {
  background: var(--sidebar-tab-active-background-color, var(--active-color, var(--gray-2, #f2f3f5)));
}
.taroify-sidebar-tab__children {
  word-break: break-all;
}
.taroify-sidebar-tab--active {
  font-weight: var(--sidebar-tab-active-font-weight, var(--font-weight-bold, 500));
  color: var(--sidebar-active-color, var(--text-color, var(--gray-8, #323233)));
}
.taroify-sidebar-tab--active, .taroify-sidebar-tab--active:active {
  background: var(--sidebar-active-active-background-color, var(--white, #fff));
}
.taroify-sidebar-tab--active::before {
  position: absolute;
  top: 50%;
  left: 0;
  width: var(--sidebar-active-border-width, 8px);
  height: var(--sidebar-active-border-height, 32px);
  content: "";
  background: var(--sidebar-active-border-color, var(--primary-color, #1989fa));
  transform: translateY(-50%);
}
.taroify-sidebar-tab--disabled {
  color: var(--sidebar-tab-disabled-color, var(--sidebar-disabled-color, var(--gray-5, #c8c9cc)));
  cursor: not-allowed;
}
.taroify-sidebar-tab--disabled:active {
  background: var(--sidebar-tab-disabled-background-color, var(--sidebar-tab-background-color, var(--background-color, var(--gray-1, #f7f8fa))));
}