.taroify-tree-select {
  position: relative;
  display: flex;
  font-size: var(--tree-select-font-size, var(--font-size-md, 28px));
  user-select: none;
}
.taroify-tree-select__sidebar {
  flex: 1;
  overflow-y: auto;
  background: var(--tree-select-sidebar-background-color, var(--background-color, var(--gray-1, #f7f8fa)));
  -webkit-overflow-scrolling: touch;
}
.taroify-tree-select__content {
  flex: 2;
  overflow-y: auto;
  background: var(--tree-select-content-background-color, var(--white, #fff));
  -webkit-overflow-scrolling: touch;
}

.taroify-tree-select-option {
  position: relative;
  padding: var(--tree-select-option-padding, 0 64px 0 var(--padding-md, 32px));
  font-weight: var(--tree-select-option-font-weight, var(--font-weight-bold, 500));
  line-height: var(--tree-select-option-line-height, 96px);
  cursor: pointer;
}
.taroify-tree-select-option--active {
  color: var(--tree-select-option-active-color, var(--primary-color, #1989fa));
}
.taroify-tree-select-option--active .taroify-tree-select-option__icon {
  position: absolute;
  top: 50%;
  right: var(--tree-select-option-icon-active-right, var(--padding-md, 32px));
  margin-top: calc(var(--padding-xs, 16px) * -1);
  font-size: var(--tree-select-option-icon-active-font-size, 32px);
}
.taroify-tree-select-option:active {
  background: var(--tree-select-option-active-background-color, var(--active-color, var(--gray-2, #f2f3f5)));
}
.taroify-tree-select-option--disabled {
  color: var(--tree-select-option-disabled-color, var(--gray-5, #c8c9cc));
  cursor: not-allowed;
}
.taroify-tree-select-option--disabled:active {
  background: transparent;
}

.taroify-tree-select-tab {
  padding: var(--tree-select-tab-padding, 28px var(--padding-sm, 24px));
}